import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';
export class ReminderService {
    /**
     * @returns ReminderDto OK
     * @throws ApiError
     */
    static list() {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/api/reminder',
        });
    }
}
