import l from '../../localisation';
import { PaymentFrequency } from "../../gen";
import "./ExperienceType";
import "./HouseholdCostType";
import './WealthBuildingType';
import '../client/CarType';
import { createEmptyClientDto } from "../../service/defaults";
let $l;
l.subscribe(v => $l = v);
export const createNeedsAssessment = () => {
    return {
        client: createEmptyClientDto(),
        insurances: {
            accidentInsurance: {
                clientHas: false,
                clientWishes: false,
                contractor: {},
            },
            disabilityInsurance: {
                clientHas: false,
                clientWishes: false,
                contractor: {},
            },
            homeInsurance: {
                clientHas: false,
                clientWishes: false,
                contractor: {},
                homeOwnership: false,
                home: {
                    builtArea: '',
                    floors: '',
                    roofType: '',
                    cellar: false,
                    pool: false,
                    photovoltaic: false,
                    photovoltaicRemarks: '',
                    specialAssets: '',
                },
                householdInsurance: false,
                liabilityInsurance: false,
            },
            legalProtectionInsurance: {
                clientHas: false,
                clientWishes: false,
                contractor: {},
            },
            deathInsurance: {
                clientHas: false,
                clientWishes: false,
                contractor: {},
            },
            illnessInsurance: {
                clientHas: false,
                clientWishes: false,
                contractor: {},
            },
            retirementInsurance: {
                clientHas: false,
                clientWishes: false,
                contractor: {},
            },
            careRemarks: '',
        },
        wealthBuilding: {
            retirementProvision: { desired: false },
            saveBig: { desired: false },
            saveEducation: { desired: false },
            otherInvestmentGoal: { desired: false },
            alreadyInvested: { desired: false },
            paymentOnce: { desired: false },
            paymentMonthly: { desired: false },
            paymentYearly: { desired: false },
        },
        cars: [],
        experiences: [
            { name: $l.needsAssessment.experiences.lifeInsurance },
            { name: $l.needsAssessment.experiences.investmentFonds },
            { name: $l.needsAssessment.experiences.bonds },
            { name: $l.needsAssessment.experiences.shareHolding },
            { name: $l.needsAssessment.experiences.options },
            { name: $l.needsAssessment.experiences.financing },
            { name: $l.needsAssessment.experiences.guarantees },
            { name: $l.needsAssessment.experiences.properties },
            { name: $l.needsAssessment.experiences.certificates }
        ],
        householdCosts: [
            { name: $l.needsAssessment.householdBudget.rent, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
            { name: $l.needsAssessment.householdBudget.electricity, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
            { name: $l.needsAssessment.householdBudget.telecommunications, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
            { name: $l.needsAssessment.householdBudget.costOfLiving, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
            { name: $l.needsAssessment.householdBudget.cars, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
            { name: $l.needsAssessment.householdBudget.maintenanceObligation, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
            { name: $l.needsAssessment.householdBudget.leisure, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
        ],
        householdCostAdditions: [],
        householdIncome: { name: $l.needsAssessment.householdBudget.salary, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
        householdAdditionalIncome: { name: $l.needsAssessment.householdBudget.additionalIncome, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
        householdIncomeAlimony: { name: $l.needsAssessment.householdBudget.alimony, has: false, value: 0, frequency: PaymentFrequency.MONTHLY },
        conceptValue: 0,
        powerOfAttorny: {
            date: today()
        },
        legalNotice: {
            date: today()
        }
    };
};
function today() {
    let yourDate = new Date();
    const offset = yourDate.getTimezoneOffset();
    yourDate = new Date(yourDate.getTime() - (offset * 60 * 1000));
    return yourDate.toISOString().split('T')[0];
}
