import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';
export class ClientService {
    /**
     * @param requestBody
     * @returns string
     * @throws ApiError
     */
    static add(requestBody) {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/api/client/add',
            body: requestBody,
            mediaType: 'application/json',
            responseHeader: 'location',
        });
    }
    /**
     * @param clientId
     * @returns any OK
     * @throws ApiError
     */
    static delete(clientId) {
        return __request(OpenAPI, {
            method: 'DELETE',
            url: '/api/client/delete/{clientId}',
            path: {
                'clientId': clientId,
            },
        });
    }
    /**
     * @param id
     * @returns ClientDto OK
     * @throws ApiError
     */
    static get(id) {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/api/client/get/{id}',
            path: {
                'id': id,
            },
        });
    }
    /**
     * @returns ClientDto OK
     * @throws ApiError
     */
    static list() {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/api/client/list',
        });
    }
    /**
     * @returns ClientDto OK
     * @throws ApiError
     */
    static listAll() {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/api/client/list-all',
        });
    }
    /**
     * @param requestBody
     * @returns any OK
     * @throws ApiError
     */
    static update(requestBody) {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/api/client/update',
            body: requestBody,
            mediaType: 'application/json',
        });
    }
}
