<script lang="ts">
    import l from '../../localisation'
    import Button, { Label, Icon } from '@smui/button';
    import Textfield from '@smui/textfield';
    import StylizedCheckbox from '../StylizedCheckbox.svelte';
    import { NeedsAssessmentType } from './NeedsAssessmentType';
    import PaymentFrequencyComponent from '../PaymentFrequencySelection.svelte';
    import EuroTextfield from '../EuroTextfield.svelte';
    import { HouseholdCostType } from './HouseholdCostType';
    import { PaymentFrequency } from '../../gen/models/PaymentFrequency';
    import HouseholdCalculator from './HouseholdCalculator.svelte';

    export let assessment: NeedsAssessmentType;

    function addHouseholdCost() {
        let items = assessment.householdCostAdditions;
        let item: HouseholdCostType = { name: '', has: true, value: 0, frequency: PaymentFrequency.MONTHLY };
        items.push(item)
        assessment.householdCostAdditions = items;
    }

    function removeHouseholdCost(item: HouseholdCostType) {
        let items = assessment.householdCostAdditions;
        assessment.householdCostAdditions = items.filter(value => item !== value);
    }

</script>

<h2 class="mdc-typography--headline5">{$l.needsAssessment.householdBudget.expenses}</h2>

{#each assessment.householdCosts as householdCost}
<div class="container">
<StylizedCheckbox label={householdCost.name} labelStyle="mdc-typography--headline7" bind:value={householdCost.has}>
    <div class="content" style="padding-bottom: 100px;">
        <EuroTextfield bind:integer={householdCost.value} edit={true}/>
        <PaymentFrequencyComponent bind:paymentFrequency={householdCost.frequency}/>
    </div>
</StylizedCheckbox>
</div>
{/each}

{#each assessment.householdCostAdditions as householdCost}
<div class="container">
    <Button on:click={() => removeHouseholdCost(householdCost)}>
        <Icon class="material-icons">close</Icon>
    </Button>
    <div class="content" style="padding-bottom: 100px;">
        <Textfield bind:value={householdCost.name} label={$l.remarks} input$emptyValueUndefined={true} input$emptyValueNull={true} style="width: 100%;"/>
        <EuroTextfield bind:integer={householdCost.value} edit={true}/>
        <PaymentFrequencyComponent bind:paymentFrequency={householdCost.frequency}/>
    </div>
</div>
{/each}
<Button on:click={() => addHouseholdCost()}>
    <Icon class="material-icons">add</Icon>
    <Label>{$l.add}</Label>
</Button>

<h2 class="mdc-typography--headline5" style="padding-top: 50px;">{$l.needsAssessment.householdBudget.income}</h2>

<div class="container">
<StylizedCheckbox label={assessment.householdIncome.name} labelStyle="mdc-typography--headline7" bind:value={assessment.householdIncome.has}>
    <div class="content" style="padding-bottom: 100px;">
        <EuroTextfield bind:integer={assessment.householdIncome.value} edit={true}/>
        <PaymentFrequencyComponent bind:paymentFrequency={assessment.householdIncome.frequency}/>
    </div>
</StylizedCheckbox>
</div>

<div class="container">
<StylizedCheckbox label={assessment.householdAdditionalIncome.name} labelStyle="mdc-typography--headline7" bind:value={assessment.householdAdditionalIncome.has}>
    <div class="content" style="padding-bottom: 100px;">
        <EuroTextfield bind:integer={assessment.householdAdditionalIncome.value} edit={true}/>
        <PaymentFrequencyComponent bind:paymentFrequency={assessment.householdAdditionalIncome.frequency}/>
    </div>
</StylizedCheckbox>
</div>

<div class="container">
<StylizedCheckbox label={assessment.householdIncomeAlimony.name} labelStyle="mdc-typography--headline7" bind:value={assessment.householdIncomeAlimony.has}>
    <div class="content">
        <EuroTextfield bind:integer={assessment.householdIncomeAlimony.value} edit={true}/>
        <PaymentFrequencyComponent bind:paymentFrequency={assessment.householdIncomeAlimony.frequency}/>
    </div>
</StylizedCheckbox>
</div>

<HouseholdCalculator {assessment}/>

<h2 class="mdc-typography--headline5" style="padding-top: 50px;">{$l.needsAssessment.householdBudget.conceptValue}</h2>
<div style="padding-bottom: 100px;">
    <EuroTextfield bind:integer={assessment.conceptValue} edit={true}/>
    <Textfield value={$l.payment.monthly} label={$l.payment.paymentFrequency} input$emptyValueUndefined={true} input$emptyValueNull={true} style="width: 100%;"/>
</div>


<style lang="scss">
    .container {
        width: 100%;
        display: flex;
        align-items: center;
    }
    .content {
        padding: 20px 50px 20px 50px;
    }
</style>