<script lang="ts">
    import l from '../../localisation'
    import { ClientContractDto, PaymentFrequency } from '../../gen';
    import { HouseholdCostType } from './HouseholdCostType';
    import { InsuranceType, NeedsAssessmentType } from './NeedsAssessmentType';

    export let assessment: NeedsAssessmentType;

    function getIncome(assessment: NeedsAssessmentType) : number {
        let sum = 0;
        sum += getMonthlyHouseholdCost(assessment.householdIncome)
        sum += getMonthlyHouseholdCost(assessment.householdAdditionalIncome)
        sum += getMonthlyHouseholdCost(assessment.householdIncomeAlimony)

        return sum;
    }

    function getMonthlyHouseholdCost(cost: HouseholdCostType) {
        return getMonthly(cost.frequency, cost.value);
    }

    function getMonthly(frequency: PaymentFrequency, value: number) {
        if(frequency === PaymentFrequency.MONTHLY) {
            return value;
        }
        if(frequency === PaymentFrequency.QUARTERLY) {
            return value / 3;
        }
        if(frequency === PaymentFrequency.YEARLY) {
            return value / 12;
        }
        return 0;
    }

    function getMonthlyInsurance(insurance: InsuranceType & ClientContractDto) {
        if(insurance.clientHas) {
            return getMonthly(insurance.paymentFrequency, insurance.paymentValue);
        }
        return 0;
    }

    function getExpense(assessment: NeedsAssessmentType) : number {
        let sum = 0;
        for(const cost of assessment.householdCosts) {
            sum += getMonthlyHouseholdCost(cost)
        }
        for(const cost of assessment.householdCostAdditions) {
            sum += getMonthlyHouseholdCost(cost)
        }
        sum += getMonthlyInsurance(assessment.insurances.accidentInsurance);
        sum += getMonthlyInsurance(assessment.insurances.disabilityInsurance);
        sum += getMonthlyInsurance(assessment.insurances.homeInsurance);
        sum += getMonthlyInsurance(assessment.insurances.legalProtectionInsurance);
        sum += getMonthlyInsurance(assessment.insurances.deathInsurance);
        sum += getMonthlyInsurance(assessment.insurances.retirementInsurance);
        sum += getMonthlyInsurance(assessment.insurances.illnessInsurance);

        return sum;
    }

    function format(value: number) : string {
        return (value / 100).toFixed(2).replace(".", ",");
    }
    $: income = getIncome(assessment);
    $: expenses = getExpense(assessment);
    $: difference = income - expenses;
</script>

<h2 class="mdc-typography--headline5" style="padding-top: 50px;">{$l.needsAssessment.householdBudget.overview}</h2>

<div class="result">
    <span class="result-label">{$l.needsAssessment.householdBudget.sum}&nbsp;{$l.needsAssessment.householdBudget.income}</span><span class="result-value">€&nbsp;{format(income)}</span>
    <span class="result-label">{$l.needsAssessment.householdBudget.sum}&nbsp;{$l.needsAssessment.householdBudget.expenses}</span><span class="result-value">€&nbsp;{format(expenses)}</span>
    <span class="result-label">{$l.needsAssessment.householdBudget.difference}</span><span class="result-value {income>=expenses?'good':'bad'}">€&nbsp;{format(difference)}</span>
</div>

<style lang="scss">
    .result {
        display: grid;
        grid-template-columns: 1fr 1fr;
        row-gap: 10px;
    }
    .good {
        font-weight: 800;
        color: forestgreen;
    }
    .bad {
        font-weight: 800;
        color: red;
    }
</style>