import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';
export class ClientContractService {
    /**
     * @param requestBody
     * @returns ClientContractDto OK
     * @throws ApiError
     */
    static add(requestBody) {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/api/client-contract/add',
            body: requestBody,
            mediaType: 'application/json',
        });
    }
    /**
     * @param id
     * @returns any OK
     * @throws ApiError
     */
    static delete(id) {
        return __request(OpenAPI, {
            method: 'DELETE',
            url: '/api/client-contract/delete/{id}',
            path: {
                'id': id,
            },
        });
    }
    /**
     * @param contractId
     * @returns any OK
     * @throws ApiError
     */
    static getPolicyRequest(contractId) {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/api/client-contract/get-policy-request/{contractId}',
            path: {
                'contractId': contractId,
            },
        });
    }
    /**
     * @param contractId
     * @returns any OK
     * @throws ApiError
     */
    static getPolicy(contractId) {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/api/client-contract/get-policy/{contractId}',
            path: {
                'contractId': contractId,
            },
        });
    }
    /**
     * @param requestBody
     * @returns any OK
     * @throws ApiError
     */
    static update(requestBody) {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/api/client-contract/update',
            body: requestBody,
            mediaType: 'application/json',
        });
    }
    /**
     * @param formData
     * @returns ClientContractDto OK
     * @throws ApiError
     */
    static uploadPolicy(formData) {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/api/client-contract/upload-policy',
            formData: formData,
            mediaType: 'multipart/form-data',
        });
    }
    /**
     * @param formData
     * @returns ClientContractDto OK
     * @throws ApiError
     */
    static uploadPolicyRequest(formData) {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/api/client-contract/upload-policy-request',
            formData: formData,
            mediaType: 'multipart/form-data',
        });
    }
}
